.auth_page{
    width: 100%;
    padding: 2.5rem 2rem;
    display: flex;
    justify-content: center;
  }
  
  .auth_page .auth_box{
    max-width: 400px;
    width: 100%;
    padding: 2.7rem 1.7rem;
  }
  
  .auth_page .pass{
    position: relative;
  }
  .auth_page .pass small{
    position: absolute;
    top: 50%;
    right: 5px;
    transform: translateY(-50%);
    cursor: pointer;
    opacity: 0.5;
  }

  .auth_page input::placeholder{
    opacity: 0.5;
    font-size: 80%;
  }
  
  .auth_page a{
    text-decoration: none;
  }

  .auth_page a:hover{
    text-decoration: underline;
  }